.header {
    margin-top: 20px;
    display: flex;
    flex: 1;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: #121212;
  }
  
  .header-top {
    display: flex;
    width: 100%;
    height: 80px;
  }
  
  .header-top__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
  }
  
  .header-logo {
    display: flex;
    color: inherit;
    text-decoration: inherit;
    font-family: Aboreto-Regular;
    font-size: 90px;
    flex: 1;
    color:#E1E3E4
  }
  
  .header-top__navbar {
    display: flex;
    flex: 1;
    flex-direction: column;
  }
  
  .header-top__separator {
    width: 100%;
    border: none;
    height: 2px;
    margin-top: 2px;
    background-color: #E1E3E4;
 }
  
  .header-top__navigation {
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 55px;
    color:#E1E3E4
  }
  
  .header-bottom {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    align-items: center;
    margin-top: 6px;
    flex-wrap: wrap;
  }
  
  .header-bottom__email {
    margin-right: 55px;
    display: flex;
    color: #E1E3E4;
    flex-wrap: wrap;

  }
  
  .header-bottom__phone {
    margin-right: 65px;
    display: flex;
    color: #E1E3E4
  }
  
  .header-bottom__bag {
    position: absolute;
    display: flex;
  }
  
  .header-bottom__bag-icon {
    position: relative;
    display: flex;
    left: -5px;
    width: 30px;
    height: 30px; 
  }
  
  .header-bottom__bag-count {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Roboto;
    right: 0px;
    top: -5px;
    width: 20px;
    height: 20px;
    background-color: red;
    border-radius: 10px;
    color: white;
  }

@media (max-width: 1000px) {
    .header-logo {
        font-size: 50px
    }
    .header-bottom__email, .header-bottom__phone {
        position: relative;
        top: 6px;

    }
}
@media (max-width:600px) {

.header-logo {
    font-size: 50px;
    }
    .header-bottom__email, .header-bottom__phone {
        position: relative;
        top: 6px;

    }
}